.music-player {
  background-color: #181818;
  color: white;
  padding: 16px;
  display: flex;
  height: 7vh;
  align-items: center;
}
.volume-controller {
  flex-grow: 1;
  display: flex;
  text-align: center;
  align-items: center;
}
.playback-controller {
  flex-grow: 1;
}
.time-stamp {
  font-size: 11px;
  color: var(--gray);
  padding-inline: 8px;
}
.seeker {
  display: flex;
  text-align: center;
  align-items: center;
}
.playback-slider {
  flex-grow: 1;
}
@media screen and (max-width: 768px) {
  .volume-controller {
    display: none;
  }
}
