.song-info {
  display: flex;
  align-items: center;
  width: 30%;
}
.text-info {
  align-self: center;
}
.song-name {
  padding-left: 13px;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 16px;
  padding-bottom: 2px;

  width: 180px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.artist-name {
  padding-left: 12px;
  padding-top: 1px;
  font-size: 11px;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 16px;
  color: var(--gray);

  width: 180px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.song-image-mini {
  height: 64px;
  width: 64px;
  object-fit: cover;
}
@media screen and (max-width: 768px) {
  .song-info {
    display: none;
  }
}
