.song-card {
  flex-grow: 1;
  flex: 1 1 360px;
  max-width: 360px;
  background-color: rgb(255, 255, 255, 0.03);
  border-radius: 1rem;
  text-align: center;
  padding: 1rem;
  margin: 1rem;
}

.song-card:hover {
  background-color: rgb(255, 255, 255, 0.07);
}

.song-poster {
  height: 300px;
  width: 300px;
}

@media screen and (max-width: 768px) {
  .song-poster {
    height: 200px;
    width: 200px;
  }
}
